.c-planes{
    margin-top: 4rem;

    padding: 0rem 2rem;
    display: flex;
    flex-direction: column;
    gap: 4rem;
    position: relative;
    
}
.plans{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3rem;
}
.plan{
    display: flex;
    flex-direction: column;
    background-color: var(--caloryCard);
    color: white;
    gap: 2rem;
width: 15rem;
padding: 1.5rem;}
.plan:nth-child(2){
    background: var(--planCard);
    transform: scale(1.1);
}
.plan>:nth-child(2){
    font-size: 1rem;
    font-weight: bold;
}
.plans>:nth-child(2)>svg{
 fill: white;
}
.plans>:nth-child(2)>button{
  color: orange;
}
.plan>:nth-child(3){
    font-size: 3rem;
    font-weight: bold;
}
.plan>:nth-child(5){
    font-size: .8rem
}

.plan>svg{
    fill: var(--orange);
    width: 2rem;
    height: 2rem;
}
.features{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.feature{

    align-items: center;
    display: flex;
    gap: 1rem;
}
.feature>img{width: 1rem;}
@media (max-width:760px) {
    .plans{
        flex-direction: column;
    }
    .plan:nth-child(2){
        transform: none;
    }
    .p-header{
        font-size: large;
        display: flex;
        flex-direction: column;
    }
}