/* make variables */
:root {
  --lightgray: #D9D9D9;
  --gray: #9c9c9c;
  /* --orange: #f48915; */
    --orange: #09f805;
  --darkGrey: #464D53;
  --caloryCard: #656565;
  --planCard: linear-gradient(210.41deg, #04621b 3.14%, #07f907 100.75%);
  /* --planCard: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%); */
  --appColor:  #3c3f45;
}

.App {
  /* background-image: url(data:image/png;base64,); */
  /* background: linear-gradient(to right, rgba(238, 5, 5, 0.208)20%,rgba(254, 104, 4, 0.431)97%);  */
  background: linear-gradient(to right, rgba(10, 99, 1, 0.208)20%,rgba(4, 254, 58, 0.431)97%);
  mix-blend-mode: overlay;
  background-color: var(--appColor);
  display: flex;
  flex-direction: column;
  gap: 6rem;
  overflow: hidden;
}
::-webkit-scrollbar{
  display: none;
}
.s-text{
  color: transparent;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-text-stroke-width:1px;
  -webkit-text-stroke-color:white;

}
 .jn:hover{
  cursor: pointer;
  transform: rotate(360deg);
  transition: ease-in all 0.7s;
}